@import url(https://fonts.googleapis.com/css2?family=Long+Cang&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pontano+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:'Pontano Sans', sans-serif}body{background-color:#B1B1B1}.logo{color:#400013;font-family:'Long Cang', cursive;font-size:3rem;margin-left:30px}.container{max-width:1000px;margin:0 auto}

.home{display:flex;flex-direction:column;padding:30px}.home .cavia_home-img{align-self:center}

header{background-image:url(/Cavia-LAB/static/media/cavia-header.0ea42480.jpg);background-position:center;background-repeat:no-repeat;background-size:cover;height:50vh}header .header_container{display:flex}

nav{display:flex;flex-direction:column;justify-content:center;margin:0 auto;background-color:#DEDEDE}nav .mobile-menu{font-size:2rem;display:block;align-self:center}@media (min-width: 700px){nav .mobile-menu{display:none}}nav .hide{display:none}@media (min-width: 700px){nav .hide{display:flex;align-items:center}}nav .desktop{display:block}nav ul{display:flex;flex-direction:column;list-style:none;justify-content:center;align-items:center;padding:0}@media (min-width: 700px){nav ul{display:flex;justify-content:space-around;flex-direction:row;list-style:none;align-items:center;padding-left:40px}}nav ul li{line-height:2rem}.nav_link{text-decoration:none;text-transform:uppercase}@media (min-width: 700px){.nav_link{text-decoration:none;padding-right:75px;text-transform:uppercase}}

footer{background-color:#DEDEDE}footer .footer_container{display:flex;justify-content:space-between;margin:0 auto;align-items:center}footer .footer_rights{margin-left:30px}footer .footer_icons{margin-right:30px}footer .facebook_icon{font-size:2rem;padding-right:10px;color:royalblue}footer .twitter_icon{font-size:2rem;color:royalblue}

.about_container{display:flex;flex-direction:column;padding:30px}

.animals_img_container{display:flex;flex-wrap:wrap;justify-content:space-evenly}.animals_img_container .animals_img{height:300px;width:300px;border-radius:50%;margin:30px auto;background-position:center;background-size:cover;background-repeat:no-repeat;display:flex;justify-content:center;align-items:center;text-transform:uppercase;font-size:1.5rem}.animals_img_container .animal_link{text-decoration:none;color:white}.animals_img_container .smooth-haired{background-image:url(/Cavia-LAB/static/media/gładkowłosa.38fddfa7.jpg)}.animals_img_container .peruvian{background-image:url(/Cavia-LAB/static/media/peruwianka.9f63be62.jpg)}.animals_img_container .rosette{background-image:url(/Cavia-LAB/static/media/rozetka.0e954858.jpg)}.animals_img_container .teddy{background-image:url(/Cavia-LAB/static/media/teddy.cdf1d828.jpg)}.animals_img_container .skinny{background-image:url(/Cavia-LAB/static/media/skinny.d6e64ec9.jpeg)}

.contact{padding:40px}.contact__column{margin-bottom:50px;background-color:#F4F4F4;display:flex;flex-direction:column;justify-content:center;align-items:center}.details__item{text-decoration:none;color:black;margin-bottom:10px}.details__item img{margin-right:10px}.contact__details{display:flex;flex-direction:column}.form{display:flex;justify-content:center;flex-direction:column;align-items:center}.form__field{display:flex;flex-direction:column;justify-content:center;align-items:center}.form__field-text{text-align:center}.form__input{background-color:#F4F4F4;border:none;border-bottom:1px solid blue;margin-bottom:20px}.btn{background-color:dodgerblue;width:100px;height:50px;border-radius:35px;display:block;margin-bottom:-25px}.form__validation{display:flex;justify-content:center;flex-direction:column;align-items:center}.contact_address{text-align:center;background-color:#F4F4F4;padding:30px}.sent_confirm{color:forestgreen}.error_alert{color:red}

.animals_container{display:flex;flex-direction:column}.animals_list{background-color:#C6C6C6;display:flex;margin:15px;max-width:1000px}.brand_img{height:200px;width:200px}@media (min-width: 700px){.brand_img{height:400px;width:400px}}.brand_list{list-style:none;display:flex;flex-direction:column;justify-content:space-evenly;font-size:1.2rem}

